"use strict";
module.exports = function postEvent(chartId, isIframe) {
    const host = isIframe ? window.parent : window;
    return function (event, data) {
        if (host && host.postMessage) {
            const evt = {
                source: 'datawrapper',
                chartId,
                type: event,
                data
            };
            host.postMessage(evt, '*');
        }
    };
};
